/* Helpers: */

.center-childs {
  display: flex;
  justify-content: center;
  align-items: center;
}

.hide {
  display: none;
}

.debug {
  outline: 3px solid black;
  background-color: blanchedalmond;
}

.clean-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

/* Flex-helpers: */

.flex {
  display: flex;
}
.column {
  flex-direction: column;
}
.justify-center {
  justify-content: center;
}
.align-center {
  align-items: center;
}
.space-between {
  justify-content: space-between;
}
.align-start {
  align-items: flex-start;
}
.fg-1 {
  flex-grow: 1;
}

/* common: */

.card {
  display: inline-block;
  padding: 20px 40px;
  border-radius: 5px;
  box-shadow: 0 1px 1px #67676747;
  background-color: white;
  transition: box-shadow 0.2s;
  &.clickable {
    cursor: pointer;
    &:hover {
      box-shadow: 0px 2px 2px 0px #67676740;
    }
  }
}
.card.dark {
  background-color: #2c3e50;
  color: white;
  border: unset;
}

.card.danger {
  color: #e74c3c;
}
